import React, {useEffect, useState } from "react";
import Product1 from "../../assets/img/product-burger.jpg"
import Product2 from "../../assets/img/product-pizz.jpg"
import Product3 from "../../assets/img/product-cartofi.jpg"
import Product4 from "../../assets/img/product-coca-cola.jpg"
import Product5 from "../../assets/img/salad-1.png"
import Api from "../../api";

const MenuProducts = () => {
    
    let [products, setProducts] = useState([]);

    let img = [Product1, Product2, Product3 ,Product4, Product5];

    let api = new Api();

    let populateProducts = async()=>{
        let rez = await api.getAllProducts();
        if(typeof rez != 'string'){
            setProducts(rez);
        }else{
            console.log(rez);
        }
    }

    let handleFilter = async(e)=>{
        e.preventDefault();
        let obj = e.target;

        if(obj.id){
            // setare active pe element
            let parent = obj.parentNode;
            let links = parent.childNodes;
            links.forEach(e=>{
                if(e.classList.contains("active")){
                    e.classList.remove("active");
                }
            })
            obj.classList.add("active");
            
            if(obj.id != "All"){
                let rez = await api.getAllOrderDetailsFiltred(obj.id);
                setProducts(rez);
            }
            if(obj.id == "All"){
                populateProducts();
            }

        }
    }

    useEffect(()=>{
        populateProducts();
    },[]);

    return (
        <section className="menu-products">

            <div className="header">
                <div className="title">
                    <h1>Our Products</h1>
                    <div className="line"></div>
                </div>
                
                <div className="lists" onClick={handleFilter}>
                    <a href="#" id="All" className="active">All</a>
                    <a href="#" id="Pizza">Pizza</a>
                    <a href="#" id="Burgers">Burgers</a>
                    <a href="#" id="Fries">Fries</a>
                    <a href="#" id="Salad">Salad</a>
                    <a href="#" id="Drinks">Drinks</a>
                </div>
            </div>

            <div className="body">
                {
                    products && 
                    products.map(e=>{
                        if(e.fk_category_id){
                            return <div key={e.id} className="card">
                                <img src={img[e.fk_product_id_product_category.id-1]} alt=""/>
                                <h1>{e.fk_product_id_product_category.name}</h1>
                                <p>{e.fk_product_id_product_category.description}</p>
                                <h2>${e.fk_product_id_product_category.price}</h2>
                                <a href="#">ORDER NOW</a>
                            </div>
                        }else{
                            return <div key={e.id} className="card">
                                <img src={img[e.id-1]} alt=""/>
                                <h1>{e.name}</h1>
                                <p>{e.description}</p>
                                <h2>${e.price}</h2>
                                <a href="#">ORDER NOW</a>
                            </div>
                        }
                    })
                }
            </div>

            <a href="#" className="view-more">VIEW MORE</a>
        </section>
    )
}

export default MenuProducts